* {
  --header-height: 60px;
  --nav-font-size: 12px;
  --nav-is-active-line-height: 4px;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  min-height: var(--header-height);
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo-link {
  text-decoration: none;
}

.logo {
  width: 40px;
  height: 40px;
  display: flex;
}

.nav {
  align-items: center;
  display: flex;
}

.nav-list {
  flex-wrap: wrap;
  align-items: center;
  gap: 20px;
  list-style: none;
  display: flex;
}

.list-item {
  list-style: none;
}

.item-link {
  text-align: center;
  color: #000;
  font-size: var(--nav-font-size);
  line-height: var(--header-height);
  text-decoration: none;
  display: block;
  position: relative;
}

.is-active, .item-link:hover {
  color: #00acdc;
}

.is-active:after {
  content: "";
  height: var(--nav-is-active-line-height);
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  display: block;
  position: absolute;
  bottom: 0;
}
